import Vue from 'vue'
import Vuex from 'vuex'
import _user from './modules/user' // 用户信息
import _power from './modules/power' // 权限
import getters from './getters'

Vue.use(Vuex)

// modules留着扩展
const store = new Vuex.Store({
  modules: {
    user: _user,
    power: _power
  },
  getters: getters
})

export default store



