import $https from '@/utils/https'

// 读取文件
export function readFile (params) {
  return $https({
    url: '/file/read',
    method: 'GET',
    params: params
  })
}

// 根据手机号获取校验码
export function getCheckCodeByPhone (params) {
  return $https({
    url: '/workOrder/getCheckCodeByPhone',
    method: 'POST',
    params: params
  })
}

// 手机验证码登录
export function checkCodeLogin (params) {
  return $https({
    url: '/workOrder/checkCodeAndPhone',
    method: 'POST',
    params: params
  })
}

// 获取维修类型
export function getMaintenanceTypes () {
  return $https({
    url: '/workOrder/maintenanceTypes',
    method: 'POST'
  })
}

// 提交维保申请
export function submitWorkOrder (params) {
  return $https({
    url: '/workOrder/submitWorkOrder',
    method: 'POST',
    params: params
  })
}

// 查询提交保单列表
export function listWorkOrders (params) {
  return $https({
    url: '/workOrder/listWorkOrders',
    method: 'POST',
    params: params
  })
}

// 查询提交保单详情
export function detailWorkOrder (params) {
  return $https({
    url: '/workOrder/detailWorkOrder',
    method: 'POST',
    params: params
  })
}
// 确认返厂
export function confirmReturnFactory (params) {
  return $https({
    url: '/workOrder/confirmReturnFactory',
    method: 'POST',
    params: params
  })
}



