import Axios from 'axios'
import store from '../store/index'
import api from '../config/api'
import router from '../router'
import {Toast} from 'vant'

// 全局请求接口配置信息
Axios.defaults.baseURL = api.url
// Axios.defaults.headers['Content-Type'] = 'multipart/form-data'
Axios.defaults.headers['Content-Type'] = 'application/json'
Axios.defaults.timeout = 60 * 1000
// Axios.defaults.headers.common['X-Requested-with'] = 'XMLHttpRequest'
// Axios.defaults.headers.common['yhToken'] = ''
// Axios.defaults.headers.common['Authorization'] = ''
let _haveLoading = true
let _isShow = false
const _appId = window.ServerConfig.appId

// Request拦截器
Axios.interceptors.request.use(
  config => {
    // 处理全局参数注入Token
    let _timestamp = +new Date() + ''
    
    if (config.method === 'post') {
      config.data['appId'] = _appId || ''
      config.data['signType'] = 'plain'
      config.data['encType'] = 'plain'
      config.data['timestamp'] = _timestamp
      config.data['version'] = '1.0.0'
      config.data['transType'] = ''

      if (store.getters.token) {
        config.data['token'] = store.getters.token
      }
      // if (api.contentType === 'application/x-www-form-urlencoded') {
      // form提交时需转换数据为字符串
      // config.data = qs.stringify(config.data)
      // }
    }
    if (config.method === 'get') {
      if (store.getters.token) {
        config.params['token'] = store.getters.token
      }
    }
    return config
  },
  err => {
    return Promise.reject(err)
  }
)

// Response拦截器
Axios.interceptors.response.use(
  response => {
    // 处理关闭loading
    _haveLoading && (Indicator.close())
    // axios默认请求判断状态
    if (response.status === 200) {
      // 成功过滤处理，返回数据对象，属性信息走配置文件的配置
      if (!api.responseConfig.isOK(response.data) || api.responseConfig.errorCodes.indexOf(response.data[api.responseConfig['code']]) >= 0) {
        // 请求出错，统一处理
        if (!_isShow) {
          _isShow = true
          Toast({message: `${response.data[api.responseConfig.msg] || '请求数据出错'}`, duration: 2000})
          setTimeout(() => {
            api.responseConfig.errorCallback(store, router)
            _isShow = false
          }, 1200)
        }
        return Promise.reject(response.data)
      } else {
        return Promise.resolve(response.data)
      }
    } else {
      return Promise.reject(response.data)
    }
  },
  error => {
    // 处理关闭loading
    _haveLoading && (Indicator.close())
    if (error.response) {
      // 处理错误状态码
      switch (error.response.status) {
        case 401:
          break
        default:
          break
      }
    }
    return Promise.reject(error.response.data)
  }
)

// 包装https请求方法，做统一的请求控制
export default function https ({url = api.url, isLoading = true, method = 'POST', serviceId = '', params = {}}) {
  _haveLoading = isLoading
  if (isLoading) {
    Indicator.open({text: '数据处理中...'})
  }
  return new Promise((resolve, reject) => {
    let _axiosConfig = {
      method: method,
      params: {},
      data: {},
      url: url
    }
    if (method.toUpperCase() === 'GET') {
      // _axiosConfig['params']['serviceId'] = serviceId
      _axiosConfig['params'] = params
    } else {
      _axiosConfig['data']['serviceId'] = serviceId
      _axiosConfig['data']['data'] = params
    }
    Axios(_axiosConfig).then(res => {
      resolve(res)
    }).catch(err => {
      reject(err)
    })
  })
}



