import $https from '@/utils/https'

// 登录接口
export function login (params) {
  return $https({
    url: 'api/login',
    method: 'POST',
    serviceId: 'api.login',
    params: params
  })
}

// 测试
export function getDemoList (params) {
  return $https({
    url: '/api/user/list',
    method: 'GET',
    serviceId: 'api.getList',
    params: params
  })
}



