import Cookies from 'js-cookie'
import * as $services from '@/services/index'
import api from "../../config/api"

let _userInfo = Cookies.get('userInfo')
_userInfo = _userInfo && JSON.parse(_userInfo)
let _hospitalInfo = Cookies.get('hospitalInfo')
_hospitalInfo = _hospitalInfo && JSON.parse(_hospitalInfo)

const user = {
  state: {
    uid: Cookies.get('userId'),
    token: Cookies.get('userToken'),
    userInfo: _userInfo,
    hospitalInfo: _hospitalInfo,
    userPhone: ''
  },
  mutations: {
    setUid: (state, uid) => {
      state.uid = uid
    },
    setToken: (state, token) => {
      state.token = token
    },
    setUserInfo: (state, userInfo) => {
      state.userInfo = userInfo
    },
    setHospitalInfo: (state, hospitalInfo) => {
      state.hospitalInfo = hospitalInfo
    },
    setUserPhone: (state, userPhone) => {
      state.userPhone = userPhone
    }
  },
  actions: {
    // 登录接口
    Login ({commit}, userInfo) {
      return new Promise((resolve, reject) => {
        $services.login(userInfo).then(res => {
          commit('setToken', res.token)
          commit('setUserInfo', res)
          // 设置cookie
          Cookies.set('userToken', res.token, {expires: 1 / 3})
          Cookies.set('userId', res.id, {expires: 1 / 3})
          Cookies.set('userInfo', res, {expires: 1 / 3})
          resolve(res)
        }).catch(function (err) {
          reject(err)
        })
        return false
      })
    }
  }
}

export default user



