const getters = {
  uid: state => state.user.uid, // 用户id
  token: state => state.user.token, // 用户令牌
  userInfo: state => state.user.userInfo, // 用户所有信息
  hospitalInfo: state => state.user.hospitalInfo, // 医院相关信息
  menu: state => state.power.menu, // 菜单列表
  commonData: state => state.power.commonData, // 平台数据信息
  routers: state => state.power.routers, // 路由表
  userPhone: state => state.user.userPhone // 路由表
}

export default getters



