import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: '',
      redirect: '/login'
    },
    {
      path: '/login',
      name: 'login',
      component: resolve => require(['@/pages/login'], resolve)
    },
    {
      path: '/index',
      name: 'index',
      component: resolve => require(['@/pages/index'], resolve)
    },
    {
      path: '/parchase',
      name: 'parchase',
      component: resolve => require(['@/pages/parchase'], resolve)
    },
    {
      path: '/points',
      name: 'points',
      component: resolve => require(['@/pages/points'], resolve)
    },
    {
      path: '/refund',
      name: 'refund',
      component: resolve => require(['@/pages/refund'], resolve)
    },
    {
      path: '/user',
      name: 'user',
      component: resolve => require(['@/pages/user'], resolve)
    },
    {
      path: '/register',
      name: 'register',
      component: resolve => require(['@/pages/register'], resolve)
    },
    {
      path: '/forgetPassword',
      name: 'forgetPassword',
      component: resolve => require(['@/pages/forgetPassword'], resolve)
    },
    {
      path: '/setting',
      name: 'setting',
      component: resolve => require(['@/pages/setting'], resolve)
    },
    {
      path: '/msgSetting',
      name: 'msgSetting',
      component: resolve => require(['@/pages/msgSetting'], resolve)
    },
    {
      path: '/callback',
      name: 'callback',
      component: resolve => require(['@/pages/callback'], resolve)
    },
    {
      path: '/questions',
      name: 'questions',
      component: resolve => require(['@/pages/questions'], resolve)
    },
    {
      path: '/agreement',
      name: 'agreement',
      component: resolve => require(['@/pages/agreement'], resolve)
    },
    {
      path: '/notice',
      name: 'notice',
      component: resolve => require(['@/pages/notice'], resolve)
    },
    {
      path: '/orders',
      name: 'orders',
      component: resolve => require(['@/pages/orders'], resolve)
    },
    {
      path: '/qscode',
      name: 'qscode',
      component: resolve => require(['@/pages/qscode'], resolve)
    },
    {
      path: '/highSearch',
      name: 'highSearch',
      component: resolve => require(['@/pages/highSearch'], resolve)
    }
  ]
})



