const _BASE = window.ServerConfig.base
const _TESTBASE = window.ServerConfig.testBase
const _IMGBASE = window.ServerConfig.imgBase
const _APPID = window.ServerConfig.appId

export default {
  // 基础配置
  base: _BASE,
  testBase: _TESTBASE,
  url: _BASE + '/cfc-iot',
  uploadUrl: _BASE + 'ma-app/api/v1.0/saveFiles',
  imgUrl: _IMGBASE + '/file/read/',
  appId: _APPID,
  // 数据传输类配置
  contentType: 'application/x-www-form-urlencoded', // form提交数据：application/x-www-form-urlencoded
  transType: 'unify.check.common', // 根据需求文档确定
  signType: 'Plain', // 根据需求文档确定
  encryptType: 'Plain', // 根据需求文档确定
  termType: 'WEB_ADMIN', // 根据需求文档确定
  tokenCheck: true, // 是否开启token校验,不开启的情况下，以下属性都将无效
  // 统一请求配置
  responseConfig: {
    // 响应代码
    code: 'code',
    // 响应数据
    data: 'result',
    // 错误提示信息
    msg: 'message',
    // 响应是否是成功的
    isOK: function (response) {
      return (response instanceof Object) && (response['code'] === 0)
    },
    // tokenCheck为true时，token验证不通过时返回的异常码， 出现这种情况情况下将直接执行回调errorCallBack
    errorCodes: ['020001', '020002', '-1'],
    errorCallback: function (store, router) {
      // token过期后,自定义处理,或返回错误
      router.push({
        path: '/login'
      })
    }
  },
  // 白名单配置
  routerWhiteList: [
    'login',
    'validCode',
    'maintenance',
    'submitSuccess',
    'ordersList',
    'orderDetail',
    'payOrder',
    'paySuccess'
  ]
}



